import React, { useState, useEffect } from "react";
import Header from "./components/page-parts/Header";
import DisplayDaily from "./components/page-parts/DisplayDaily";
import DisplayWeekly from "./components/page-parts/DisplayWeekly";
import "./App.css";
import { weeklySchedule } from "./data/schedule";

const updateWeeklySchedule = () => {
  const apiUrl = "http://localhost:8000/api/v1/schedule";

  fetch(apiUrl)
    .then((response) => {
      if (response.classes) {
        weeklySchedule.length = 0;
        response.classes.forEach((item) => weeklySchedule.push(item));
      }
    })
    .catch((error) => {
      console.error(error);
    });
};

setInterval(updateWeeklySchedule, 10_000);

function App() {
  // Settings Options
  const [labName, setLabName] = useState("Lab Name");
  const [timer, setTimer] = useState(5_000);
  const [dailyMax, setDailyMax] = useState(5);
  const [weeklyMax, setWeeklyMax] = useState(4);

  // Display Daily or Weekly with animation
  const [currentComponent, setCurrentComponent] = useState("DisplayDaily");
  const [animationClass, setAnimationClass] = useState("");

  useEffect(() => {
    const interval = setInterval(() => {
      setAnimationClass("slide-out-left");

      setTimeout(() => {
        setCurrentComponent((prevComponent) =>
          prevComponent === "DisplayDaily" ? "DisplayWeekly" : "DisplayDaily"
        );
        setAnimationClass("slide-in-right");
      }, 900);
    }, timer);

    return () => {
      clearInterval(interval);
    };
  });

  return (
    <div className="m-0 border-0 h-100 w-100 text-center background">
      <Header
        stateLabName={{ labName, setLabName }}
        stateTimer={{ timer, setTimer }}
        stateDailyMax={{ dailyMax, setDailyMax }}
        stateWeeklyMax={{ weeklyMax, setWeeklyMax }}
      />

      <main className="display-container" style={{ height: "85%" }}>
        <div className={`h-100 display-item ${animationClass}`}>
          {currentComponent === "DisplayDaily" ? (
            <DisplayDaily maxDisplay={dailyMax} />
          ) : (
            <DisplayWeekly maxDisplay={weeklyMax} />
          )}
        </div>
      </main>
    </div>
  );
}

export default App;
