import { React, useState } from 'react';
import * as XLSX from 'xlsx';
import axios from 'axios';
import { weeklySchedule } from '../../data/schedule';

const XLSXReader = ({ dropboxAccessKey }) => {

  const accessToken = dropboxAccessKey;
  axios.defaults.headers.common['Authorization'] = `Bearer ${accessToken}`;

  // Dropbox API endpoint for listing files in a folder
  const dropboxAPIUrlList = 'https://api.dropboxapi.com/2/files/list_folder';

  const directoryPath = '/lab_schedules/';

  const getScheduleList = () => {

      if (!isDropdownOpen) {

        if (dropboxAccessKey) {
          axios
          .post(dropboxAPIUrlList, { path: directoryPath })
          .then(response => {
              const newSchedule = response.data.entries.map((entry) => entry.name);
              setScheduleList(newSchedule);
              setIsDropdownOpen(!isDropdownOpen);
          })
          .catch((error) => {
            setErrorMessage("Please check your access key again!"); 
            setHasError(true);
          });
        }
        else {
          setErrorMessage("Please provide an access key!"); 
          setHasError(true);
        }
      }
      else {
        setIsDropdownOpen(!isDropdownOpen);
      }
  }

  // List of items for the dropdown
  const [scheduleList, setScheduleList] = useState([]);

  // Dropbox API endpoint to fetch file content
  const dropboxAPIUrlDownload = 'https://content.dropboxapi.com/2/files/download';

  const readFileContents = async (filePath) => {
    try {
        const response = await fetch(dropboxAPIUrlDownload, {
            method: 'POST',
            headers: {
            'Authorization': 'Bearer ' + accessToken,
            'Dropbox-API-Arg': JSON.stringify({ path: filePath }),
            }
        });

        if (!response.ok) {
            throw new Error('Network response was not ok');
        }

        const arrayBuffer = await response.arrayBuffer();
        const workbook = XLSX.read(arrayBuffer, { type: 'array' });
        const sheetName = workbook.SheetNames[0];
        const worksheet = workbook.Sheets[sheetName];

        readClasses(worksheet); 
    
    } catch (error) {
        console.error('Error fetching or reading file: ', error);
    }
  };

  // Function to be triggered when an item is selected from the dropdown
  const getSchedule = (selectedItem) => {
    const filePath = directoryPath + selectedItem;
    readFileContents(filePath);
  };

  // State to track the visibility of the dropdown menu
  const [isDropdownOpen, setIsDropdownOpen] = useState(false);

  const [hasError, setHasError] = useState(false);
  const [errorMessage, setErrorMessage] = useState('');

  return (
    <div>
      <div className="d-flex justify-content-center align-items-center">
        {/* Dropdown List with Bootstrap classes */}
        <div className="dropdown">
          <button
            className="btn btn-dark dropdown-toggle"
            type="button"
            id="dropdownMenuButton"
            onClick={() => {
              // Clear error state when the dropdown is clicked
              setHasError(false);
              getScheduleList();
            }}
          >
            Select Schedule
          </button>
          <div
            className={`dropdown-menu${isDropdownOpen ? ' show' : ''}`}
            aria-labelledby="dropdownMenuButton"
          >
            {scheduleList.map((item, index) => (
              <span
                key={index}
                className="dropdown-item"
                onClick={() => {
                  getSchedule(item);
                  setIsDropdownOpen(false); // Close the dropdown after selecting an item
                }}
              >
                {item.split('.xlsx')[0]}
              </span>
            ))}
          </div>
        </div>
      </div>
      {/* Conditional rendering for error text */}
      {hasError && (
        <p className="text-danger text-center mt-2"> {errorMessage} </p>
      )}
    </div>
  );
}

const readClasses = (worksheet) => {
  const mergedCells = worksheet['!merges'] || [];
  const classes = [];
  let idCounter = 0;

  const maxRow = XLSX.utils.decode_range(worksheet['!ref']).e.r;
  const maxCol = XLSX.utils.decode_range(worksheet['!ref']).e.c;

  for (let col = 0; col <= maxCol; col++) {
      for (let row = 0; row <= maxRow; row++) {
          const cellAddress = XLSX.utils.encode_cell({ r: row, c: col });
          const cell = worksheet[cellAddress];

          if (cell) {
              const cellValue = cell.v;

              if (cellValue.includes('\n')) {
                  let rowspan = 1;

                  mergedCells.forEach((range) => {
                      const { s, e } = range;
                      const startRow = s.r;
                      const endRow = e.r;
                      const startCol = s.c;
                      const endCol = e.c;

                      if (row >= startRow && row <= endRow && col >= startCol && col <= endCol) {
                          rowspan = endRow - startRow + 1;
                      }
                  });

                  const days = ["Mon", "Tue", "Wed", "Thu", "Fri", "Sat", "Sun"];

                  const startTime = row+7;
                  const startTimeStr = startTime.toString().padStart(2, "0");
                  const endTime = row+7+rowspan;

                  

                  const timeRange = `${startTimeStr}:00-${endTime}:00`;
                  const lines = cellValue.split("\n");

                  classes.push({
                      id: idCounter++,
                      day: days[col-1],
                      time: timeRange,
                      title: lines[0],
                      professor: lines[1]
                  });
              }   
          }
      }
  }

  weeklySchedule.length = 0;
  classes.forEach((item) => weeklySchedule.push(item));
}



export default XLSXReader;
